.menu {
	position: relative;
	top: 5px;
	display: none;

	@media (max-width: 768px) {
		display: block;
	}

	button {
		background: none;
		border: none;
		cursor: pointer;
		transition: opacity 0.4s ease-in-out;

		&:hover {
			opacity: 0.5;
		}

		svg {
			width: 35px;
			height: 35px;
		}
	}
}
